header {
    display: flex;
    justify-content: center;
}

.header__image {
    width: 60%;
}

h1 {
    text-align: center;
    display: flex;
    justify-content: center;
    color: #ea4e25;
    text-shadow: var(--text-shadows);
    margin: 1px;
}

.header__tip {
    font-size: 20px;
    display: flex;
    justify-content: center;
    text-align: center;
    color: white;
    text-shadow: var(--text-shadows);
}


@media (min-width: 375px) and (max-width: 500px) {
    h1 {
        font-size: 20px;
    }

    .header__tip {
        font-size: 15px;
    }

    .header__image {
        display: none;
    }

}