.card {
  padding: 25px;
  text-align: right;
  width:  300px;
  height: 400px;
  cursor: pointer;
  perspective: 1000px;
}

.card-area {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
}

@media (min-width: 375px) and (max-width: 500px) {
  .card {
      width: 200px;
      height: 300px;
  }

}