.card-side__face-back-stats {
  width: 100%;
}

.card-side__face-back-stats-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.card-side__face-back-title-stat {
  width: 85%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card-side__face-back-stats-line {
  background-color: gray;
  border-radius: 5px;
  height: 0.4em;
  margin-bottom: 5px;
  width: 85%;
}

.card-side__face-back-stats-line-value {
  max-width: 100%;
  border-radius: 5px;
}

h3 {
  display: flex;
  justify-content: center;
  margin: 0;
}