.card-side__face {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 16px;
  box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

.card-side__face-back {
  transform: rotateY(180deg);
  background-color: white;
  -webkit-box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 8px solid var(--color-yellow);
  margin: 10px;
  filter: blur(15px);
  background: url('../../../public/img/background-card.jpg');
}

.card-side__face-back-content {
  width: 100%;
  height: 30%;
  border-radius: 7px 7px 0 0
}

.card-side__face-back-image-area {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-side__face-back-name {
  text-transform: capitalize;
  margin: 0;
}

.card-side__face-back-image {
  margin-top: 55px;
  background: white;
  border-radius: 50%;
}

.card-side__face-back-header-stats {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 90%;
}

.card-side__face-back-header-stats li {
  list-style-type: none;
}

@media (min-width: 375px) and (max-width: 500px) {
  .card-side__face-back-image-area{
    font-size: 10px;
  }

  .card-side__face-back-image {
    width: 75px;
  }
}




