.card-side {
    height: 100%;
    transition: transform 1s;
    transform-style: preserve-3d;
    cursor: pointer;
    font-weight: 100;
}

.card-side.is-flipped {
    transform: rotateY(180deg);
}

.card-side__face-front {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url(../../../public/img/background_card2.jpg);
    background-size: cover
}

.card-side__face-front-image {
    margin: 10px;
    max-width: 230px;
}

.card-side__face-front-name {
    font-size: 40px;
    color: white;
    text-shadow: var(--text-shadows);
    text-transform: capitalize;
    letter-spacing: 1px;
    margin: 10px;
    text-align: center;
}

.card__face {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 16px;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

@media (min-width: 375px) and (max-width: 500px) {
    .card-side__face-front-image {
        max-width: 150px;
    }
    
    .card-side__face-front-name {
        font-size: 30px;
    }
  
  }